import React, {Component} from 'react';
import {connect} from "react-redux";
import {endpointFieldChange} from "../../../../store/actions/endpoints";

const Action = ( props ) => {

	const { id, action, endpointFieldChange, sectionVisibility } = props;

	const endpointActions = [
		{
			name:  'Posts: Display',
			value: 'display_data',
			description: 'Use this action if you want to create an endpoint for displaying posts'
		}, {
			name:  'Posts: Create',
			value: 'create_post',
			description: 'Use this action if you want to create an endpoint for creating a post'
		},
		{
			name:  'Posts: Edit',
			value: 'edit_post',
			description: 'Use this action if you want to create an endpoint for editing a post'
		},
		{
			name:  'Taxonomies: Display',
			value: 'display_taxonomies',
			description: 'Use this action if you want to create an endpoint for displaying taxonomy terms'
		},
		{
			name:  'Users: Display',
			value: 'display_users',
			description: 'Use this action if you want to create an endpoint for displaying users'
		},
		{
			name:  'Users: Edit',
			value: 'edit_users',
			description: 'Use this action if you want to create an endpoint for editing users'
		},
		{
			name:  'Users: Create',
			value: 'create_users',
			description: 'Use this action if you want to create an endpoint creating users'
		},
		{
			name:  'Custom Table: Display',
			value: 'display_custom_table',
			description: 'Use this action if you want to create an endpoint for displaying data from your custom database tables'
		},
		{
			name:  'Custom Table: Create',
			value: 'create_custom_table',
			description: 'Use this action if you want to create an endpoint for inserting data into any custom database tables'
		},
		{
			name:  'Custom Table: Edit',
			value: 'edit_custom_table',
			description: 'Use this action if you want to create an endpoint for editing data from custom database tables'
		},
		{
			name:  'Custom Table: Delete',
			value: 'delete_custom_table',
			description: 'Use this action if you want to create an endpoint for deleting data from custom database tables'
		}
	];

	return (
		<div id="poststuff" className="action-box">
			<div id="slugdiv" className="postbox ">
				<h2 className="hndle ui-sortable-handle" onClick={ e => endpointFieldChange( !sectionVisibility, id, 'actionSection' ) }><span>Endpoint Type</span></h2>
				<div className="inside" style={{display: sectionVisibility ? 'block' : 'none'}}>
					<p>Here is where you define the heart of your endpoint. This option will unlock you features that are supposed to be used to a particular kind of endpoint.</p>
					<select value={action}>
						{endpointActions.map((m, index) => <option disabled={m.value !== 'display_data' ? 'disabled' : ''} key={m.value} value={m.value}>{m.name}</option>)}
					</select><span style={{marginTop: '3px'}} className="dashicons dashicons-lock blocked-select" />

					<p>{endpointActions.filter(el => el.value === action)[0].description}</p>

				</div>
			</div>
		</div>
	);
};

const mapStateToProps = ( state, props ) => ({
	id: props.id,
	action: state.settings.endpoints[props.id].action,
	sectionVisibility: state.settings.endpoints[props.id].actionSection,
});

const mapDispatchToProps = dispatch => ({
	endpointFieldChange: ( value, endpointId, field ) => dispatch( endpointFieldChange( value, endpointId, field ) ),
});

export default connect( mapStateToProps, mapDispatchToProps )(Action);
