export default function getFieldFactory() {
	return {
		getSourceField() {
			return {
				id:           'source',
				inputType:    'select',
				source:       'valueSource',
				label:        'Source',
				sourceName:   'name',
				sourceValue:  'value',
				value:        '',
				defaultValue: 'Select a source',
			};
		},
		getParametersField() {
			return {
				id:           'parameters',
				inputType:    'select',
				label:        'Parameter',
				source:       'parameters',
				sourceName:   'name',
				sourceValue:  'name',
				value:        '',
				defaultValue: 'Select a parameter',
			};
		},
		getDynamicField() {
			return {
				id:           'dynamicValues',
				inputType:    'select',
				label:        'Dynamic Value',
				source:       'dynamicValues',
				sourceName:   'name',
				sourceValue:  'value',
				value:        '',
				defaultValue: 'Select a dynamic field',
			};
		},
		getValueInputType( inputType = 'input', label = 'Value' ) {
			return {
				id:        'value',
				inputType: inputType,
				value:     '',
				label:     label,
			};
		},
		getOnlyLabel( name, label ) {
			return {
				name:   name,
				label:  label,
				fields: [
					{
						id:           'field',
						inputType:    '',
						source:       '',
						sourceName:   'name',
						sourceValue:  'value',
						value:        '',
						label:        '',
						defaultValue: '',
					}
				],
			}
		},
		getYesNoFieldGroup( name, label ) {
			return {
				name: name,
				label: label,
				fields: [
					getFieldFactory().getSourceField(),
					{
						id: 'value',
						inputType: 'select',
						source: 'yesNo',
						sourceName: 'name',
						sourceValue: 'value',
						value: '',
						label: label,
						defaultValue: 'Select an option',
					},
					getFieldFactory().getParametersField(),
				],
			};
		},
		getNewDefaultFieldGroup(name, label, fieldType = 'input', labelValue = 'Value') {
			return {
				name:   name,
				label:  label,
				fields: [
					this.getSourceField(), this.getValueInputType( fieldType, labelValue ), this.getParametersField(), this.getDynamicField(),
				],
			};
		},
		getPostTypes(inputType) {
			return {
				name:   'post_type',
				label:  'Post type',
				fields: [
					this.getSourceField(), {
						id:           'value',
						inputType:    inputType,
						source:       'postTypes',
						sourceName:   'name',
						sourceValue:  'value',
						value:        '',
						label:        'Value',
						defaultValue: 'Select a post type',
					}, this.getParametersField(),
				],
			}
		},
		getStatusFieldGroup(inputType) {
			return {
				name:   'post_status',
				label:  'Post status',
				fields: [
					this.getSourceField(), {
						id:           'value',
						inputType:    inputType,
						source:       'statuses',
						sourceName:   'name',
						sourceValue:  'value',
						value:        '',
						label:        'Value',
						defaultValue: 'Select a status',
					}, this.getParametersField(),
				],
			}
		},
		getRolesFieldGroup(inputType, name = 'roles', label = 'Roles') {
			return {
				name:   name,
				label:  label,
				fields: [
					this.getSourceField(), {
						id:           'value',
						inputType:    inputType,
						source:       'roles',
						sourceName:   'name',
						sourceValue:  'value',
						value:        '',
						label:        'Value',
						defaultValue: 'Select a role',
					}, this.getParametersField(),
				],
			}
		}
	}
}
