function changeGroupField(state, action ) {
	return {
		...state,
		settings: {
			...state.settings,
			endpoints: [
				...state.settings.endpoints.map((el, index) => {
					if ( index === action.payLoad.endpointId ) {
						el[action.payLoad.group][action.payLoad.index][action.payLoad.field] = action.payLoad.value
					}
					return el;
				}),
			]
		}
	};
}

export { changeGroupField };