import React, {Component} from 'react';
import getFieldFactory from "../../Endpoint/FieldFactory";
import FieldGroups from "../FieldGroups";
import {connect} from "react-redux";
import {addSubField} from "../../../../../store/actions/subFields";
import {FieldGroupContext} from "../../../../../contexts/FieldGroupContext";

class Output extends Component {

	state = {
		selectedFilterToAdd: ''
	};

	handleSelectedFilterToAdd = (e) => {
		this.setState({selectedFilterToAdd: e.target.value});
	};

	render() {

		const {addSubField, group} = this.props;

		const fields = [
			getFieldFactory().getOnlyLabel( 'ID', 'Post ID' ),
			getFieldFactory().getOnlyLabel( 'post_title', 'Post title' ),
			getFieldFactory().getOnlyLabel( 'post_author', 'Post author' ),
			getFieldFactory().getOnlyLabel( 'post_date', 'Post date' ),
			getFieldFactory().getOnlyLabel( 'post_date_gmt', 'Post date GMT' ),
			getFieldFactory().getOnlyLabel( 'post_content', 'Post content' ),
			getFieldFactory().getOnlyLabel( 'post_excerpt', 'Post excerpt' ),
			getFieldFactory().getOnlyLabel( 'post_status', 'Post status' ),
			getFieldFactory().getOnlyLabel( 'comment_status', 'Comment status' ),
			getFieldFactory().getOnlyLabel( 'ping_status', 'Ping status' ),
			getFieldFactory().getOnlyLabel( 'post_password', 'Post password' ),
			getFieldFactory().getOnlyLabel( 'post_name', 'Post name' ),
			getFieldFactory().getOnlyLabel( 'to_ping', 'To ping' ),
			getFieldFactory().getOnlyLabel( 'pinged', 'Pinged' ),
			getFieldFactory().getOnlyLabel( 'post_modified', 'Post modified' ),
			getFieldFactory().getOnlyLabel( 'post_modified_gmt', 'Post modified GMT' ),
			getFieldFactory().getOnlyLabel( 'post_content_filtered', 'Post content filtered' ),
			getFieldFactory().getOnlyLabel( 'post_parent', 'Post parent' ),
			getFieldFactory().getOnlyLabel( 'guid', 'GUID' ),
			getFieldFactory().getOnlyLabel( 'permalink', 'Permalink' ),
			getFieldFactory().getOnlyLabel( 'menu_order', 'Menu order' ),
			getFieldFactory().getOnlyLabel( 'post_type', 'Post type' ),
			getFieldFactory().getOnlyLabel( 'post_mime_type', 'Post mime type' ),
			getFieldFactory().getOnlyLabel( 'comment_count', 'Comment count' ),
      getFieldFactory().getOnlyLabel( 'post_format', 'Post format' ),
			{
				name:   'custom-field',
				label:  'Custom field',
				fields: [
					{
						id:        'customFieldName',
						inputType: 'input',
						value:     '',
						label:     'Custom field name',
						dynamic:   false,
					},
				],
			},
			{
				name: 'taxonomy',
				label: 'Taxonomy',
				fields: [
					{
						id: 'taxonomy',
						inputType: 'select',
						source: 'taxonomies',
						sourceName: 'name',
						sourceValue: 'value',
						value: '',
						label: 'Taxonomy',
						defaultValue: 'Select a taxonomy',
						dynamic: false
					},
					{
						id: 'field-type',
						inputType: 'checkboxes',
						source: 'termFieldTypes',
						sourceName: 'name',
						sourceValue: 'value',
						value: '',
						label: 'Fields',
						defaultValue: 'Select a field type',
						dynamic: false
					},
					{
						id:        'termFields',
						inputType: 'input',
						value:     '',
						label:     'Term fields (separated by comma)',
						dynamic:   false,
					},
				],
			},
			getFieldFactory().getOnlyLabel( 'featured_image', 'Featured image' ),
			getFieldFactory().getOnlyLabel( 'attached_images', 'Attached images' ),
			getFieldFactory().getOnlyLabel( 'attached_audios', 'Attached audios' ),
			getFieldFactory().getOnlyLabel( 'attached_videos', 'Attached videos' ),
		];

		return (
			<React.Fragment>
				<FieldGroupContext.Consumer>

					{({ endpointId, endpoint, currentItem, action }) => (<React.Fragment>
						<h3>Fields to display in the output: ({endpoint.displayData[currentItem].output.length})</h3>

						Select a field to output: <select value={this.state.selectedFilterToAdd} onChange={this.handleSelectedFilterToAdd}>
							<option value="">Select a field</option>
							{fields.sort((a, b) => (a.label > b.label) ? 1 : -1).map((el, index) => <option key={index} value={el.name}>{el.label}</option>)}
						</select>

						<button
							onClick={() => addSubField(endpointId, fields.filter(el => el.name === this.state.selectedFilterToAdd), currentItem, group, action)}
							disabled={!this.state.selectedFilterToAdd ? 'disabled' : ''}
							className="button-secondary"
						>
							Add field
						</button>

						<FieldGroups group={group} />

					</React.Fragment>)}
				</FieldGroupContext.Consumer>
			</React.Fragment>
		);
	}
}

const mapStateToProps = ( state ) => ( state );

const mapDispatchToProps = dispatch => ({
	addSubField: ( endpointId, field, currentItem, group, parentGroup ) => dispatch( addSubField( endpointId, field, currentItem, group, parentGroup ) ),
});

export default connect( mapStateToProps, mapDispatchToProps )(Output);
