import React, {Component} from 'react';
import { connect } from "react-redux";
import {endpointFieldChange} from "../../../../store/actions/endpoints";

class Method extends Component {

	render() {

		const { method, id, endpointFieldChange, sectionVisibility } = this.props;

		const methods = [
			{
				name:  'Select a method',
				value: '',
				disabled: false
			},{
				name:  'Readable (GET)',
				value: 'GET',
				disabled: false
			}, {
				name:  'Creatable (POST)',
				value: 'POST',
				disabled: false
			}, {
				name:  'Editable (POST, PUT, PATCH)',
				value: 'POST, PUT, PATCH',
				disabled: false
			}, {
				name:  'Deletable (DELETE)',
				value: 'DELETE',
				disabled: false
			}
		];

		return (
			<div id="poststuff" className="method-box">
				<div id="slugdiv" className="postbox ">
					<h2 className="hndle ui-sortable-handle" onClick={ e => endpointFieldChange( !sectionVisibility, id, 'methodSection' ) }><span>Select a method</span></h2>
					<div className="inside" style={{display: sectionVisibility ? 'block' : 'none'}}>
						<p>Choose one of the available methods below. Do not forget to follow the good REST practices for HTTP methods.</p>
						<select value={method}>
							{methods.map((m, index) => <option key={index} disabled={m.value !== 'GET' ? 'disabled' : ''} value={m.value}>{m.name}</option>)}
						</select><span style={{marginTop: '3px'}} className="dashicons dashicons-lock blocked-select" />
					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = ( state, props ) => ({
	method: state.settings.endpoints[props.id].method,
	methods: state.existingMethods,
	sectionVisibility: state.settings.endpoints[props.id].methodSection,
});

const mapDispatchToProps = dispatch => ({
	endpointFieldChange: ( value, endpointId, field ) => dispatch( endpointFieldChange( value, endpointId, field ) ),
});

export default connect( mapStateToProps, mapDispatchToProps )(Method);
